

import React from "react";
import { NavLink } from "react-router-dom"
import { connect } from "react-redux";
import {
  addData1,
  addData2,
  removeData1,
  removeData2,
  updateData1
} from '../store/actions'
const parse = require("html-react-parser");

const env = require('./env')
class Grid extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            _asset_url: env.data.asset_url,
            _grid_style: true,
            selected_id: ''
        };
    }
    componentDidMount() {
        console.log(this.props.Data1[0])
    }
    changeView = (val) => {
        this.setState({ _grid_style: val })
    }
    gotoRound = (id) => {
        document.getElementById(id).scrollIntoView();
        // this.setState({selected_id:id})
    }
    render() {
        return (
            <div>
                <div class="quiz-grid-block">
                    <div class="quiz-grid-popup">
                        <div class="d-flex flex-column justify-content-between h-100">
                            <div class="quiz-grid-column">
                                <div class="quiz-grid-header-wrapper" onClick={() => this.props.grid_click(false)}>
                                    <div class="quiz-grid-header">JEE 2020 Revision: Thermal Energy Properties of Matter Quiz</div>
                                </div>
                                <div class="quiz-grid-time-overview">
                                    <div class="test-topic-resp grid-resp">
                                        <div class="test-topic-questionscount">
                                            {this.props.Data1[0].length} Questions
                  </div>
                                        <div class="test-total-time">15 Minutes</div>
                                    </div>
                                    <div class="quiz-grid-icon-acnblk">
                                        <div class={!this.state._grid_style ? 'quiz-grid-icon-container nav-active' : 'quiz-grid-icon-container'} onClick={() => this.changeView(false)}>
                                            <img src="img/menu-toggler.svg" class="menu-toggler" />
                                        </div>
                                        <div class={this.state._grid_style ? 'quiz-grid-icon-container nav-active' : 'quiz-grid-icon-container'} onClick={() => this.changeView(true)}>
                                            <img src="img/nav-toggler.svg" class="nav-toggler" />
                                        </div>
                                    </div>
                                </div>
                                {(this.state._grid_style &&
                                    <div class="quiz-grid-qno-wrapper">
                                        <div class="quiz-grid-qno-container">
                                            {this.props.Data1[0].map((val, index) => {
                                                return (
                                                    <div key={index} onClick={() => { this.gotoRound(val.id) }} class={(this.state.selected_id == val.id) ? 'ques-bubble mb-23 ques-active' : 'ques-bubble mb-23'}>{index + 1}</div>
                                                )
                                            })}
                                        </div>
                                    </div>
                                )}


                                {/* lk */}
                                {(!this.state._grid_style &&
                                    <div class="quiz-grid-list-wrapper">
                                        {this.props.Data1[0].map((val, index) => {
                                            return (
                                                <div class="quiz-grid-list-container">
                                                    <div class="quiz-grid-qno">
                                                        <div key={index} onClick={() => { this.gotoRound(val.id) }} class={(this.state.selected_id == val.id) ? 'ques-bubble ques-active' : 'ques-bubble'}>{index + 1}</div>
                                                    </div>
                                                    <div class="quiz-grid-list">
                                                        {parse(val.q_question[1])}
                                                        {/* NodeJs-The Complete Guide (incl. MVC, REST APIs,Graphing) */}
                                                    </div>
                                                </div>
                                                // <div key={index} onClick={() => { this.gotoRound(val.id) }} class={(this.state.selected_id == val.id) ? 'ques-bubble mb-23 ques-active' : 'ques-bubble mb-23'}>{index + 1}</div>
                                            )
                                        })}
                                    </div>
                                )}

                            </div>
                            <div class="quiz-grid-markings">
                                <div class="quiz-grid-marking-wrapper">
                                    <div class="quiz-grid-mark-container">
                                        <div class="quiz-mark-iconblk answered"></div>
                                        <div class="quiz-mark-desc">Answered</div>
                                    </div>
                                    <div class="quiz-grid-mark-container">
                                        <div class="quiz-mark-iconblk not-visited"></div>
                                        <div class="quiz-mark-desc">Not Visited</div>
                                    </div>
                                </div>
                                <div class="quiz-grid-marking-wrapper">
                                    <div class="quiz-grid-mark-container">
                                        <div class="quiz-mark-iconblk unanswered"></div>
                                        <div class="quiz-mark-desc">Unanswered</div>
                                    </div>
                                    <div class="quiz-grid-mark-container">
                                        <div class="quiz-mark-ribbon"><img src="img/ribbon_marked.svg" /></div>
                                        <div class="quiz-mark-desc">Marked Qstn</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default connect(
    ({ Data1, Data2 }) => ({ Data1, Data2 }),
    {
        addData1,
        addData2,
        removeData1,
        removeData2,
        updateData1
    }
  )(Grid)