import React from "react";
import axios from "axios";
import SingleChoice from "./qz_single_choice";
import MultipleChoice from "./qz_multiple_choice";
import InstructionsOne from "./gusture_itstruction1"
import InstructionsTwo from "./gusture_itstruction2"
import Header from "./header"
import Grid from "./grid_type"
import SubmitPopup from "./submit_popup"
import GeneralInstructions from "./general_instructions"
import { connect } from "react-redux";
import {
  addData1,
  addData2,
  removeData1,
  removeData2,
  updateData1
} from '../store/actions'
import './swipe'
// const env = require("./quiz");

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      _quiz_json: "",
      _loader: <div style={{ textAlign: "center", marginTop: " 50%" }}>Loading...</div>,
      _generalInstruction: false,
      _showQuestions: false,
      _gestureInstructionOne: false,
      _gestureInstructionTwo: false,
      _submitPopupOpen: false,
      _quiz_id_key: '',
      _questions: [],
      _timer: '',
      _htmlData: <div><p>NO data Found</p></div>
    };
  }

  getdb(key) {
    return new Promise(async (resolve, reject) => {
      try {
        var dataValue = await window.ldb.get(key);
        resolve(dataValue)
      } catch (e) {
        console.log(e);
      }
    })

  }
  setdb = (key,value) => {
    window.ldb.set(key, JSON.stringify(value));

  }
  async componentDidMount() {
    if (localStorage.getItem("quiz_id") == null) {
      await this.callApi()
    } else {
      let quiz_id_key = localStorage.getItem("quiz_id")
      this.setState({
        _quiz_json: JSON.parse(await this.getdb("data")), _quiz_id_key: quiz_id_key
      });
      console.log(JSON.parse(await this.getdb("data")))
      let data = JSON.parse(await this.getdb("data"))
      await this.arrangeQuestions(data)
    }
    this.setState({ _loader: "", _generalInstruction: true });
    // document.getElementById('root').addEventListener('swl', this.swipeLeft, false);
    // document.getElementById('root').addEventListener('swr', this.swipeRight, false);
  }

  swipeLeft = (e) => {
    this.setState({ _grid: true })
  }
  swipeRight = (e) => {
    this.setState({ _grid: false })
  }

  async callApi() {
    // await axios.get(`http://ec2-35-170-185-221.compute-1.amazonaws.com:5000/?file=quiz_json.json`)
    let data = ''
    let res = await axios.get('https://qrpkf5fao2.execute-api.ap-south-1.amazonaws.com/dev')
      .then(res => {
        // console.log(res.data.body)
        let account_id = "NYR"
        let test_id = "123"
        let fullData = JSON.stringify(res.data.body)
        // let fullData = env.quiz_json
        localStorage.setItem("quiz_id", account_id + test_id);
        // console.log(this.getdb())
        data = JSON.parse(fullData)
        console.log(res.data.body)
        // localStorage.setItem(account_id + test_id,res.data.body)
        this.arrangeQuestions(res.data.body)
        // localStorage.setItem(account_id + test_id, JSON.stringify(fullData))
        // this.arrangeQuestions(fullData)
        this.setState({
          _quiz_json: fullData, _quiz_id_key: account_id + test_id
        });
      })
    console.log(data)
    await this.setdb("data",data)
  }
  arrangeQuestions = (shuffledData) => {
    // console.log(shuffledData)
    return new Promise((resolve, reject) => {
      let question_order = shuffledData.questions_order
      let arranged_questions = []
      question_order.map((element, index) => {
        // let qn_obj={
        //   id:shuffledData.questions[element].id,
        //   q_type:shuffledData.questions[element].q_type,
        //   q_positive_mark:shuffledData.questions[element].q_positive_mark,
        //   q_negative_mark:shuffledData.questions[element].q_negative_mark,
        //   q_question:shuffledData.questions[element].q_question[1],
        //   ribbon_marked:"",
        //   q_explanation:shuffledData.questions[element].q_explanation[1],
        //   q_answer:shuffledData.questions[element].q_answer,
        //   options:shuffledData.questions[element].options

        // }
        arranged_questions.push(shuffledData.questions[element])
      });
      this.props.addData1(arranged_questions)

      // this.setState({ _questions: arranged_questions })
      resolve()
    })
  }
  gridClick = (val) => {
    this.setState({ _grid: val })
  }
  openQuestions = () => {
    this.setState({ _showQuestions: true, _generalInstruction: false, _gestureInstructionOne: true });
  };
  closeModal = () => {
    this.setState({ _gestureInstructionOne: false });
  }
  gotoGustureOne_ = (e) => {
    this.setState({ _showQuestions: true, _generalInstruction: false, _gestureInstructionOne: true });
  }
  gotoGustureTwo_ = (e) => {
    this.setState({ _showQuestions: true, _generalInstruction: false, _gestureInstructionTwo: true });
  }
  OkGotIt_ = (e) => {
    this.setState({ _showQuestions: true, _gestureInstructionOne: false, _gestureInstructionTwo: false });
    this.handletimer()
  }

  handletimer = (minute = 40) => {
    var countDownDate = new Date(new Date().getTime() + minute * 60000);
    var x = setInterval(() => {
      var now = new Date().getTime();
      var distance = countDownDate - now;
      var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      var seconds = Math.floor((distance % (1000 * 60)) / 1000);
      this.setState({ _timer: minutes + "m " + seconds + "s " })
      if (distance < 0) {
        clearInterval(x);
        this.setState({ _timer: "EXPIRED" })
      }
    }, 1000);
    // document.addEventListener("visibilitychange", function () {
    //   if (document.hidden) {
    //     clearInterval(x)
    //     console.log("Browser tab is hidden")
    //   } else {
    //     console.log("Browser tab is visible")
    //   }
    // });
  }

  openSubmitPopup = () => {
    this.setState({ _submitPopupOpen: true })
  }

  updateToLocalStorage_multiple_choice(qn_id, option_id, correct_optn_id, optn_index) {
    let localVlaue = JSON.parse(localStorage.getItem(this.state._quiz_id_key))
    if (localVlaue.attended_questions.length >= 0) {
      localVlaue.attended_questions = {}
      let data = {}
      data[option_id] = { option_id: option_id, correct_option_id: correct_optn_id }
      localVlaue.attended_questions[qn_id] = data
      localVlaue.questions[qn_id].options[optn_index]['class'] = "ques-choice-marked"
      console.log("1111")
    } else if (!(qn_id in localVlaue.attended_questions == true)) {//true if not contain key qn_id
      let data = {}
      data[option_id] = { option_id: option_id, correct_option_id: correct_optn_id }
      localVlaue.attended_questions[qn_id] = data
      localVlaue.questions[qn_id].options[optn_index]['class'] = "ques-choice-marked"
      console.log("2222")
    } else {
      if (!(option_id in localVlaue.attended_questions[qn_id] == true)) {//true if not contain key option_id
        localVlaue.attended_questions[qn_id][option_id] = { option_id: option_id, correct_option_id: correct_optn_id }
        localVlaue.questions[qn_id].options[optn_index]['class'] = "ques-choice-marked"
        console.log("333")
      } else {
        if (localVlaue.attended_questions[qn_id][option_id] != undefined) {
          if (localVlaue.attended_questions[qn_id][option_id].option_id == option_id) {

            console.log("4444")
            delete localVlaue.questions[qn_id].options[optn_index]['class']
            localVlaue.attended_questions[qn_id][option_id].option_id = ""

          } else {
            localVlaue.attended_questions[qn_id][option_id] = { option_id: option_id, correct_option_id: correct_optn_id }
            localVlaue.questions[qn_id].options[optn_index]['class'] = "ques-choice-marked"
            console.log("5555")
          }
        }
      }
    }
    localStorage.setItem(this.state._quiz_id_key, JSON.stringify(localVlaue))
  }
  async updateToLocalStorage_single_choice(qn_id, option_id, correct_optn_id, optn_index, updated_question) {
    let localVlaue = JSON.parse(await this.getdb("data"))
    localVlaue.questions[qn_id] = updated_question
    if (localVlaue.attended_questions.length != undefined) {
      localVlaue.attended_questions = {}
    }
    let data = {}
    data.qn_id = qn_id
    data.option_id = option_id
    data.correct_answer = correct_optn_id
    localVlaue.attended_questions[qn_id] = data
    await this.setdb("data",localVlaue)

    // localStorage.setItem(this.state._quiz_id_key, JSON.stringify(localVlaue))
  }
  MultipleChoiceResponse = (qn_id, ans_id, correct_optn_id, qn_index, optn_index) => {
    if ( this.props.Data1[0][qn_index - 1].options[optn_index]['class'] == "ques-choice-marked") {
       this.props.Data1[0][qn_index - 1].options[optn_index]['class'] = ""
    } else {
       this.props.Data1[0][qn_index - 1].options[optn_index]['class'] = "ques-choice-marked"
    }
    this.setState({ _questions:  this.props.Data1[0] })
    this.updateToLocalStorage_multiple_choice(qn_id, ans_id, correct_optn_id, optn_index)
  }
  SingleChoiceResponse = (qn_id, ans_id, correct_optn_id, qn_index, optn_index, a, b, c) => {
     this.props.Data1[0][qn_index - 1].options[a]['class'] = ""
     this.props.Data1[0][qn_index - 1].options[b]['class'] = ""
     this.props.Data1[0][qn_index - 1].options[c]['class'] = ""
     this.props.Data1[0][qn_index - 1].options[optn_index]['class'] = "ques-choice-marked"
    //  console.log(this.props.Data1[0])
    // this.setState({ _questions:  this.props.Data1[0] })
    this.updateToLocalStorage_single_choice(qn_id, ans_id, correct_optn_id, optn_index,  this.props.Data1[0][qn_index - 1])
  }
  FlagResponse = (qn_id, qn_index) => {
    if ( this.props.Data1[0][qn_index - 1].q_question[2] == "ribbon_marked") {
       this.props.Data1[0][qn_index - 1].q_question[2] = ""
    } else {
       this.props.Data1[0][qn_index - 1].q_question[2] = "ribbon_marked"
    }
    // this.setState({ _questions:  this.props.Data1[0] })
    // console.log("here",  this.props.Data1[0])
    this.updateToLocalStorage_ribbonStatus(qn_id,  this.props.Data1[0][qn_index - 1])

  }
  async updateToLocalStorage_ribbonStatus(qn_id, updated_question) {
    let localVlaue = JSON.parse(await this.getdb("data"))
    localVlaue.questions[qn_id] = updated_question
    await this.setdb("data",localVlaue)
    // localStorage.setItem(this.state._quiz_id_key, JSON.stringify(localVlaue))
  }

  render() {
    return (
      <div>
        {this.state._generalInstruction && (
          <GeneralInstructions gotoGustureOne={this.gotoGustureOne_} />
        )}
        {this.state._showQuestions && (
          <div>
            <Header submit_clicked={this.openSubmitPopup} total_questions={ this.props.Data1[0]} grid_click={this.gridClick} timer={this.state._timer} />
            {
              this.props.Data1[0].map((qstn, index) => {
                if (qstn.q_type == 1) {
                  return (
                    <div key={index}>
                      <SingleChoice question={qstn} index={index} questions={ this.props.Data1[0]} response={this.SingleChoiceResponse} flag_response={this.FlagResponse} />
                    </div>
                  )
                }
                if (qstn.q_type == 2) {
                  return (
                    <div key={index}>
                      <MultipleChoice question={qstn} index={index} questions={ this.props.Data1[0]} response={this.MultipleChoiceResponse} flag_response={this.FlagResponse} />
                    </div>
                  )
                }
                // console.log(qstn)
              })
            }
          </div>
        )}
        {this.state._submitPopupOpen && (
          <SubmitPopup cancel_click={()=>this.setState({ _submitPopupOpen: false })}/>
        )}
        {this.state._gestureInstructionOne && (
          <InstructionsOne gotoGustureTwo={this.gotoGustureTwo_} />
        )}
        {this.state._gestureInstructionTwo && (
          <InstructionsTwo OkGotIt={this.OkGotIt_} />
        )}
        {this.state._grid && (
          <Grid grid_click={this.gridClick} total_questions={ this.props.Data1[0]} />
        )}
      </div>
    );
  }
}


export default connect(
  ({ Data1, Data2 }) => ({ Data1, Data2 }),
  {
      addData1,
      addData2,
      removeData1,
      removeData2,
      updateData1
  }
)(Home)