import React from "react";
const parse = require("html-react-parser");

export default class SingleChoice extends React.Component {

  state = {
    question: '',
    questions: [],
    index: ''
  };

  componentDidMount() {
    // console.log(this.props.question)
    this.setState({ question: this.props.question })
    this.setState({ questions: this.props.questions })
    this.setState({ index: this.props.index + 1 })
    // document.getElementById('swipe').addEventListener('swl', this.swipeLeft, false);
    // document.getElementById('swipe').addEventListener('swr', this.swipeRight, false);
  }

  render() {
    return (
      <div>
        <section class="practice-session quiz-starting-session" id="swipe">
          <div  id={this.props.question.id} class="quiz-onscroll ps-ques-wrapper" >
            <span class="ribbon" onClick={() => this.props.flag_response(this.props.question.id, this.state.index)}>
              <img src={this.props.question.q_question[2] == "ribbon_marked" ? "img/ribbon_marked.svg" : "img/ribbon.svg"} />
            </span>
            <div class="ps-ques-header-wrapper">
              <div class="ps-ques-header-container">
                <div class="ps-ques-header">
                  Single Choice
                </div>
                <div class="ps-ques-count">
                  <span class="ps-current-ques-no">{this.state.index} /</span>
                  <span class="ps-total-ques">{this.state.questions.length}</span>
                </div>
              </div>
              <div class="ps-ques-points-container">
                <div class="ps-ques-points">+{this.state.question.q_positive_mark}</div>
                <div class="ps-ques-points">-{this.state.question.q_negative_mark}</div>
              </div>
            </div>
            <div class="ps-ques-block ">
              {parse(this.props.question.q_question[1])}
            </div>
            <div class="ps-ques-choice-section" >
              <div class={this.props.question.options[0].class ? this.props.question.options[0].class + " ps-choice-container" : " ps-choice-container"} onClick={() => this.props.response(this.props.question.id, this.props.question.options[0].id, this.props.question.q_answer, this.state.index, 0, 1, 2, 3)}>
                <div class="ps-choice-options-count">A</div>
                <div class="ps-choice-options"> {parse(this.props.question.options[0].qo_options[1])}</div>
              </div>
              <div class={this.props.question.options[1].class ? this.props.question.options[1].class + " ps-choice-container" : " ps-choice-container"} onClick={() => this.props.response(this.props.question.id, this.props.question.options[1].id, this.props.question.q_answer, this.state.index, 1, 0, 2, 3)}>
                <div class="ps-choice-options-count">B</div>
                <div class="ps-choice-options"> {parse(this.props.question.options[1].qo_options[1])}</div>
              </div>
              <div class={this.props.question.options[2].class ? this.props.question.options[2].class + " ps-choice-container" : " ps-choice-container"} onClick={() => this.props.response(this.props.question.id, this.props.question.options[2].id, this.props.question.q_answer, this.state.index, 2, 0, 1, 3)}>
                <div class="ps-choice-options-count">C</div>
                <div class="ps-choice-options"> {parse(this.props.question.options[2].qo_options[1])}</div>
              </div>
              <div class={this.props.question.options[3].class ? this.props.question.options[3].class + " ps-choice-container" : " ps-choice-container"} onClick={() => this.props.response(this.props.question.id, this.props.question.options[3].id, this.props.question.q_answer, this.state.index, 3, 0, 1, 2)}>
                <div class="ps-choice-options-count">D</div>
                <div class="ps-choice-options"> {parse(this.props.question.options[3].qo_options[1])}</div>
              </div>
              {this.props.question.options[4] && (
                <div class={this.props.question.options[4].class ? this.props.question.options[4].class + " ps-choice-container" : " ps-choice-container"} onClick={() => this.props.response(this.props.question.id, this.props.question.options[4].id, this.props.question.q_answer, this.state.index, 4)}>
                  <div class="ps-choice-options-count">E</div>
                  <div class="ps-choice-options"> {parse(this.props.question.options[4].qo_options[1])}</div>
                </div>
              )}
            </div>
             {/* <div class="radio_wrapper" >
                <label class="radio-container" onClick={() => this.props.response(this.props.question.id, this.props.question.options[0].id, this.props.question.q_answer, this.state.index, 0, 1, 2, 3)}>
                  <input  type="radio" name="choice" class="radio_btn" />
                  <span class="option">{parse(this.props.question.options[0].qo_options[1])}</span>
                  <span class="choice">A</span>
                </label>
                 <label class="radio-container" >
                  <input type="radio" name="choice" class="radio_btn"/>
                  <span class="option">option</span><span class="choice">B</span>
                </label>
                 <label class="radio-container" >
                  <input type="radio" name="choice" class="radio_btn"/>
                  <span class="option">option</span><span class="choice">C</span>
                </label>
              </div> */}
          </div>
        </section>
      </div>
    );
  }
}
